import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CommonService } from 'src/app/cmdb-common/services/common.service';
@Component({
  selector: 'app-servicetype',
  templateUrl: './servicetype.component.html',
  styleUrls: ['./servicetype.component.css']
})
export class ServicetypeComponent implements OnInit {
  siId: any;
  tabshow = false;
  index: any = 0;
  constructor(private route: ActivatedRoute,
    private router: Router,
    private commonSevice: CommonService) { }
  ngOnInit(): void {
    this.route.queryParams.subscribe(params => {
      this.siId = params['siId'];
    });
    this.getSIHeaderData();
  }
  /* navigate Route to Service List */
  gotositypeList(): void {
    this.router.navigate(['configuration/servicetypelist']);
  }
  /* get Sevice  information using  service*/
  getSIHeaderData(): void {
    this.commonSevice.siHeaderData.subscribe(data => {
      this.siId = data.siId;
      if (this.siId) {
        this.tabshow = true;
      }
    });
  }

  getTabIndex($event): void {
    this.index = $event;

  }
}
