import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { newUrl } from '../../../../environments/environment';
import { configUrl } from '../../../../environments/serviceurl';

const HttpUploadOptions = {
  headers: new HttpHeaders({ "Content-Type": 'undefined' })
}

@Injectable({
  providedIn: 'root'
})
export class ServicetypeService {

  constructor(private http: HttpClient) { }


  getServicetypeList(): Observable<any> {

    return this.http.get(`${newUrl}${configUrl.getServiceTypeListAPI}`);

  }

  getParentSI(): any {
    return this.http.get(`${newUrl}${configUrl.getParentSIAPI}`);
  }

  /* get Icon List*/

  getIconList(): any {
    return this.http.get('assets/ciicon.json');
  }

  createSIDetails(param): any {
    return this.http.post(`${newUrl}${configUrl.saveSIDetailsAPI}`, param);
  }

  getSIData(id): Observable<any> {
    return this.http.get(`${newUrl}${configUrl.getSIDataAPI}` + id);
  }

  /* Delete CI Relationship */
  deleteServicetypeList(id): Observable<any> {

    return this.http.delete(`${newUrl}${configUrl.deleteServiceTypeListAPI}` + id);
  }

  getMapCITypeList(): any {
    return this.http.get(`${newUrl}${configUrl.getServiceTypeCIAPI}`);
  }

  createMapCI(param): any {
    return this.http.post(`${newUrl}${configUrl.saveMapCIAPI}`, param);
  }

  getSImapList(id): Observable<any> {
    return this.http.get(`${newUrl}${configUrl.getSImapListAPI}` + id);
  }
  deleteMapCI(id): Observable<any> {
    return this.http.delete(`${newUrl}${configUrl.deleteMapCIAPI}` + id);
  }
  getFormInputTypeList(): any {
    return this.http.get(`${configUrl.getFormControlAPI}`);
  }

  saveFormJsonData(param): any {
    return this.http.post(`${newUrl}${configUrl.saveJsonSIDetail}`, param);
  }
  getFormSIParentJSon(param): any {

    return this.http.get(`${newUrl}${configUrl.getFormSIParentJSon}`, { params: param });
    // return this.http.get('assets/json/formdata.json');
  }

}
