import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { ServicetypeService } from '../../../services/servicetype.service';
import { CommonService } from '../../../../../cmdb-common/services/common.service';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-servicetype-add-fields',
  templateUrl: './servicetype-add-fields.component.html',
  styleUrls: ['./servicetype-add-fields.component.css']
})
export class ServicetypeAddFieldsComponent implements OnInit {
  @Output() getDragInputControl: EventEmitter<any> = new EventEmitter();
  div1: boolean = true;
  div2: boolean = false;
  div3: boolean = true;
  div4: boolean = false;
  tab: any = 'tab1';
  tab1: any;
  tab2: any;
  tab3: any;
  Clicked: boolean;
  inputControlList: any;
  siTypeName: any;
  parentSIName: any;
  getsiId: any;
  constructor(private sitypeService: ServicetypeService,
    private commonSevice: CommonService,
    private route: ActivatedRoute) {

  }

  ngOnInit(): void {
    this.getFormInputList();
    this.route.queryParams.subscribe(params => {
      this.getsiId = params['siId'];
    });
    // if (this.getciId) {
    this.getSIHeaderData();
    // }
  }

  getSIHeaderData(): void {
    this.commonSevice.siHeaderData.subscribe(data => {
      this.siTypeName = data.serviceTypeName;
      this.parentSIName = data.parentServiceTypeName;

    });
  }
  getFormInputList(): void {
    this.sitypeService.getFormInputTypeList().subscribe(data => {
      this.inputControlList = data;
    });
  }
  div1Function(check): void {
    this.div1 = true;
    this.div2 = false;
    if (check === 1) {
      this.tab = 'tab1';
    } else {
      this.tab = 'tab2';
    }

  }

  div2Function(check): void {
    this.div2 = true;
    this.div1 = false;
    if (check === 2) {
      this.tab = 'tab2';
    } else {
      this.tab = 'tab1';
    }

  }

  div3Function(check): void {
    this.div3 = true;
    this.div4 = false;
    if (check === 1) {
      this.tab = 'tab1';
    } else {
      this.tab = 'tab2';
    }

  }

  div4Function(check): void {
    this.div4 = true;
    this.div3 = false;
    if (check === 2) {
      this.tab = 'tab2';
    } else {
      this.tab = 'tab1';
    }
  }

  dragStart(e, currentinputs): void {
    this.getDragInputControl.emit(currentinputs);

  }

  dragEnd(e): void {

  }
}
