import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { newUrl } from '../../../../environments/environment';
import { configUrl } from '../../../../environments/serviceurl';


@Injectable({
  providedIn: 'root'
})
export class RelationtypeService {

  constructor(private http: HttpClient) {

  }
  getRelationShipTypeList(): Observable<any> {
    return this.http.get(`${newUrl}${configUrl.relationTypeListAPI}`);
  }

  createRelationType(param) {
    return this.http.post(`${newUrl}${configUrl.saveRelationTypeAPI}`, param);
  }
  deleteRelationShipType(id): Observable<any> {
    return this.http.delete(`${newUrl}${configUrl.deleteRelationTypeAPI}` + id);
  }

}
