import { Component, OnInit } from '@angular/core';
import { CitypeService } from '../../services/citype.service';
import { TreeNode } from 'primeng/api';
import { Router } from '@angular/router';
import { FormGroup } from '@angular/forms';
import { ConfirmationService } from 'primeng/api';
import { ToastService } from '../../../../cmdb-common/services/toast.service';
import { CommonService } from '../../../../cmdb-common/services/common.service';
import { newUrl } from '../../../../../environments/environment';
@Component({
  selector: 'app-citype-list',
  templateUrl: './citype-list.component.html',
  styleUrls: ['./citype-list.component.css'],
  providers: [ConfirmationService]
})
export class CitypeListComponent implements OnInit {

  ciListViewForm: FormGroup;
  citypelist: any;
  displayModal: boolean;
  isedit: boolean;
  cols: any;
  pathUrl: any;
  isexapand = false;

  constructor(private citypeservice: CitypeService, private router: Router,
    private confirmationService: ConfirmationService, private toastService: ToastService,
    private commonSevice: CommonService) { }

  ngOnInit(): void {

    this.pathUrl = newUrl;
    this.cols = [
      { field: 'ciTypeName', subField: 'imageLocation', subField1: 'icon', header: 'CI Type', width: '25%' },
      { field: 'description', header: 'Description', width: '60%' }
    ];
    this.getciTypeList();
  }

  /* Go back button handling*/
  gotocitypeDetails() {
    const sourceCIval = {
      ciTypeName: '',
      ciId: '',
      parentCITypeId: '',
      parentCIName: '',
      hierarchy: ''
    }
    this.commonSevice.updateCiDetailInfo(sourceCIval);
    this.router.navigate(['configuration/citype']);
  }


  getciTypeList() {
    this.commonSevice.updateLoaderStatus(true);
    this.citypeservice.getCitypeList().subscribe(data => {
      this.commonSevice.updateLoaderStatus(false);
      this.citypelist = data;
      this.citypelist.forEach(node => {
        this.expandRecursive(node, true);
      });
      this.citypelist = [...this.citypelist]
    });

  }

  /* Expand and Collapse button handling*/
  exapandORcollapse() {
    this.isexapand = !this.isexapand;
    this.citypelist.forEach(node => {
      if (this.isexapand) {
        this.expandRecursive1(node, this.isexapand);
      } else {
        this.expandRecursive(node, true);
      }
    });
    this.citypelist = [...this.citypelist]
  }
  private expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, false);
      });
    }
  }
  private expandRecursive1(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }
  /* Edit CI*/
  editRow(data): void {
    this.router.navigate(['configuration/citype'], { queryParams: { ciId: data.id } });

  }
  /* Create new child */
  createNewChild(data): void {
    const sourceCIval = {
      parentCITypeId: data.id,
      parentCIName: data.ciTypeName,
    }
    this.commonSevice.updateCiDetailInfo(sourceCIval);
    this.router.navigate(['configuration/citype']);

  }

  /* Delete CI Type*/
  deleteRow(data): any {
    this.confirmationService.confirm({
      message: 'Are you sure Want to Delete CI Type?',
      header: 'Confirmation',
      icon: 'fa fa-question-circle',

      accept: () => {
        this.commonSevice.updateLoaderStatus(true);
        this.citypeservice.deleteCITypeList(data.id).subscribe(data => {
        this.toastService.success(data.message);
        this.commonSevice.updateLoaderStatus(false);
        this.getciTypeList();
        }, err => {
          this.toastService.error(err.error.message);
          this.commonSevice.updateLoaderStatus(false);
        });

      },

      reject: () => {

      },
      acceptLabel: 'Yes'

    });

  }
}
