import { Component, OnInit } from '@angular/core'
import { CommonService } from 'src/app/cmdb-common/services/common.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-citype',
  templateUrl: './citype.component.html',
  styleUrls: ['./citype.component.css']

})
export class CitypeComponent implements OnInit {
  ciTypeName: any;
  ciId: any;
  index: any = 0;
  tabshow: boolean = false;
  displayModalTab: boolean = false;
  isNewFieldDrop: boolean = false;
  tempIndex: any;
  currentIndex: any;

  constructor(private commonSevice: CommonService,
    private route: ActivatedRoute,
    private router: Router) { }
  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      this.ciId = params['ciId'];
    });
    this.getCIHeaderData();

  }
  fieldSave() {

  }
  filedCancel() {
    this.index = 2;
  }
  fieldDiscard() {
    // alert("Discard");
  }

  getNewFieldAdd($event) {
    this.isNewFieldDrop = $event;
  }
  ciDetailstab($event) {
    if ($event.index != 1) {
      // this.displayModalTab=this.isNewFieldDrop;
    }
    this.currentIndex = 2;
  }
  gotocitypeList(): void {
    this.router.navigate(['configuration/citypelist']);
  }
  getCIHeaderData(): void {
    this.commonSevice.CiHeaderData.subscribe(data => {
      this.ciTypeName = data.ciTypeName;
      this.ciId = data.ciId;
      if (this.ciId) {
        this.tabshow = true;
      }

    });
  }
  getSaveImages($event): void {
    // console.log($event);
  }

  getTabIndex($event): void {
    this.index = $event;

  }
}
