import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ConfigurationComponent } from './configuration.component';
import { CitypeComponent } from './components/citype/citype.component';
import { RelationtypeComponent } from './components/relationtype/relationtype.component';
import { CitypeListComponent } from './components/citype-list/citype-list.component';
import { ServicetypeComponent } from './components/servicetype/servicetype.component';
import { ServicetypeListComponent } from './components/servicetype-list/servicetype-list.component'
  import { from } from 'rxjs';
const routes: Routes = [
{ path: '', component:CitypeComponent },
{ path: 'citype', component:CitypeComponent },
{ path: 'citypelist', component: CitypeListComponent },
{ path: 'relationtype', component:RelationtypeComponent },
{ path: 'servicetype', component:ServicetypeComponent },
{ path: 'servicetypelist', component:ServicetypeListComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ConfigurationRoutingModule { }
