import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { CitypeService } from '../../../services/citype.service';
import { NgxFileDropEntry, FileSystemFileEntry } from 'ngx-file-drop';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { ToastService } from '../../../../../cmdb-common/services/toast.service';
import { CommonService } from '../../../../../cmdb-common/services/common.service';
import { ActivatedRoute, Router } from '@angular/router';
import { newUrl } from '../../../../../../environments/environment';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'app-ci-header-details',
  templateUrl: './ci-header-details.component.html',
  styleUrls: ['./ci-header-details.component.css'],
  providers: [ConfirmationService],
})
export class CiHeaderDetailsComponent implements OnInit {

  div1: boolean = true;
  div2: boolean = false;
  div3: boolean = true;
  div4: boolean = false;

  tab: any = 'tab1';
  tab1: any;
  tab2: any;
  tab3: any;
  Clicked: boolean;
  ciParentList: any;
  iconsList: any;
  displayModal: boolean;
  seletedIcon: any;
  seletedImport: any;
  @Output() saveImage: EventEmitter<any> = new EventEmitter();
  @Output() sourceCItype: EventEmitter<any> = new EventEmitter();
  selectedImg: any;
  file: NgxFileDropEntry[];
  submitted = false;
  imgDiv: boolean = false;
  iconSelected: any;
  imageUpload: any;
  noIcon: boolean = true;
  yesIcon: boolean = false;
  errorImage: boolean = false;
  ciError: boolean = false;
  parentCIError: boolean = false;
  imgReqerror: boolean = false;
  parentCI: Array<string> = [];
  parentCIcode: any;
  tabIndex: number = 0;
  @Output() getTabIndexValue: EventEmitter<any> = new EventEmitter();
  getCIid: any;
  pathUrl: any;
  errorFileSize: boolean = false;
  errorFileType: boolean = false;
  isValid: number = 0;
  iconSelect: any;
  iconImport: any;
  viewIcon: any;
  hideBrowse: boolean = true;
  ciTypeDetailForm: FormGroup;
  isCiDetailEdit: boolean = false;
  currentCiName: any;
  defaultIcon: any;
  selectedCIicon: boolean = false;
  disableParent: boolean = false;
  disableBrowse: boolean = true;

  constructor(private citypeService: CitypeService,
    private formBuilder: FormBuilder,
    private toastService: ToastService,
    private commonSevice: CommonService, private router: Router,
    private confirmationService: ConfirmationService,
    private route: ActivatedRoute) {
  }
  ngOnInit(): void {
    
    this.pathUrl = newUrl;
    this.defaultIcon = 'assets/upload-icon/database-big-btn.svg';
    this.viewIcon = this.defaultIcon;
    this.iconSelect = this.viewIcon;

    this.ciTypeDetailForm = this.formBuilder.group({
      id: [''],
      ciTypeName: ['', Validators.required],
      parentCIType: ['', Validators.required],
      description: [''],
      selectedIconImage: [''],
    });

    this.getPrantCI();
    this.getIconList();
    this.formControlValueChanged();
    this.getParentData();

    this.getCIid = this.route.snapshot.queryParams['ciId'];
    if (this.getCIid) {
      this.disableParent = true;
      this.editCI(this.getCIid);
    }
  }

  formControlValueChanged() {
    this.ciTypeDetailForm.get('ciTypeName').valueChanges.subscribe(
      (mode: any) => {
        if (this.isCiDetailEdit) {
          if (this.currentCiName != mode) {
            // if ((this.parentCI).indexOf(mode) > -1) {
            //   this.ciError = true;
            // } else {
            //   this.ciError = false;
            // }
            const parentCIName = this.parentCI.findIndex(data => data.toLowerCase() === mode.toLowerCase());
            if (parentCIName !== -1) {
              this.ciError = true;
            } else {
              this.ciError = false;
            }
          }
        } else {
          const parentCIName = this.parentCI.findIndex(data => data.toLowerCase() === mode.toLowerCase());
          if (parentCIName !== -1) {
            this.ciError = true;
          } else {
            this.ciError = false;
          }
        }
      });
  }

  get fval(): any {
    return this.ciTypeDetailForm.controls;
  }
  // dropdown list
  getPrantCI(): any {
    this.citypeService.getParentCI().subscribe(data => {
      this.ciParentList = data.map((map) => {
        this.parentCI.push(map.ciTypeName);
        return { code: map.id, name: map.ciTypeName };
      });
    });
  }

  // get icon list
  getIconList(): void {
    this.citypeService.getIconList().subscribe(data => {
      this.iconsList = data;
    });
  }

  getSeletedIcon(iconurl): void {
    this.selectedCIicon = false;
    if (iconurl) {
      this.selectedCIicon = true;
    }
    this.iconSelect = iconurl;
    this.errorImage = false;
  }
  /*Save Icon function*/
  saveIconorImg(): void {
    if (this.selectedCIicon == false) {
      this.displayModal = true;
      if (this.div3) {
        this.confirmationService.confirm({
          message: 'Please select an icon',
          header: 'Alert',
          icon: 'fa fa-question-circle',
          accept: () => {
          },
          acceptLabel: 'Ok'
        });
      } else if (this.div4) {
        this.confirmationService.confirm({
          message: 'Please select an image',
          header: 'Alert',
          icon: 'fa fa-question-circle',
          accept: () => {
          },
          acceptLabel: 'Ok'
        });
      }
    } else if (this.isValid == 1 || this.isValid == 2) {
      this.displayModal = true;

    } else if (this.iconSelect || this.iconImport) {
      this.displayModal = false;
      this.imgDiv = true;
      this.noIcon = false;
      this.yesIcon = true;
      this.errorImage = false;
      this.file = [];
      this.seletedIcon = this.iconSelect;
      this.seletedImport = this.iconImport;
      this.iconSelect = '';
      this.viewIcon = '';
      if (this.seletedIcon) {
        this.seletedImport = '';
        this.viewIcon = this.seletedIcon;
        this.ciTypeDetailForm.get('selectedIconImage').setValue(this.seletedIcon);
        this.saveImage.emit(this.seletedIcon);
      } else if (this.seletedImport) {
        this.seletedIcon = '';
        this.viewIcon = '';
        this.ciTypeDetailForm.get('selectedIconImage').setValue(this.seletedImport);
        this.saveImage.emit(this.seletedImport);
      }
      if (this.viewIcon || this.seletedImport) {
        this.hideBrowse = false;
      }

    } else {
      this.displayModal = true;
      this.errorImage = true;
    }

  }
  /*Edit Icon*/
  editIcon() {
    if (this.viewIcon || this.seletedImport) {
      if (this.viewIcon) {
        this.iconSelect = this.viewIcon;
      } else {
        this.iconSelect = this.seletedImport;
      }
      this.displayModal = true;
      this.div3 = true;
      this.div4 = false;
      this.tab = 'tab1';

    }
    this.disableBrowse = true;
  }
   /* Remove Icon */
  removeIcon() {
    if (this.viewIcon || this.seletedImport) {
      this.viewIcon = '';
      this.seletedImport = '';
      this.hideBrowse = true;
      this.imgDiv = false;
      this.viewIcon = this.defaultIcon;

    }
  }
  /* Save CI type function*/
  submitCIDetail(): void {
    this.seletedIcon = this.iconSelect;
    this.seletedImport = this.iconImport;
    const uploadImage = {
      "base64": "",
      "name": "",
      "type": ""
    }

    if (this.ciError) {
      this.submitted = false;
    } else {
      this.submitted = true;
      this.ciError = false;
    }
    const parentVal = this.ciTypeDetailForm.get('parentCIType').value;
    if(parentVal == ''){
      this.parentCIError = true;
      this.submitted = false;
    }else{
      this.parentCIError = false;
      this.submitted = true;
    }
    if (this.submitted && this.ciTypeDetailForm.valid) {
      this.commonSevice.updateLoaderStatus(true);
      const requestData = { ...this.ciTypeDetailForm.value }
      this.parentCIcode = requestData.parentCIType.code;
      // requestData.id=12;
      // requestData.modifiedBy=1;
      requestData.ciTypeName = (requestData.ciTypeName).trim();
      requestData.parentCIType = this.parentCIcode;
      requestData.createdBy = 1;
      requestData.status = 'Y';
      if (requestData.parentCIType.name === 'CMDB') {
        requestData.hasParent = 'N';
      } else {
        requestData.hasParent = 'Y';

      }
      if (this.viewIcon) {
        requestData.iconType = 'path';
        requestData.imageLocation = this.viewIcon;
        requestData.file = null;

      }
      if (this.seletedImport) {
        requestData.iconType = 'import';
        requestData.imageLocation = '';
        // requestData.file= this.selectedImg;
        uploadImage['base64'] = this.seletedImport;
        uploadImage['name'] = (this.selectedImg).name;
        uploadImage['type'] = (this.selectedImg).type;
        requestData.file = uploadImage;
      }
      this.citypeService.createCIDetails(requestData).subscribe((data: any) => {
        const sourceCIval = {
          ciTypeName: data.ciTypeName,
          ciId: data.id,
          parentCITypeId: data.parentCITypeId,
          parentCIName: data.parentCIName,
          hierarchy: data.hierarchy
        }
        this.commonSevice.updateCiDetailInfo(sourceCIval);
        this.commonSevice.updateLoaderStatus(false);
        this.toastService.success(data.message);
        this.seletedIcon = '';
        this.imgDiv = false;
        this.noIcon = true;
        this.yesIcon = false;
        this.ciTypeDetailForm.reset();
        this.openNext();
        this.submitted = false;
        this.editCI(data.id);

      }, err => {
        this.displayModal = false;
        this.commonSevice.updateLoaderStatus(false);
        this.toastService.error(err.error.message);

      });
    }

  }
/* Drag and Drop handling*/
  public dropped(files: NgxFileDropEntry[]): void {
    this.file = files;
    this.errorImage = false;
    this.seletedIcon = '';
    this.iconImport = '';
    this.iconSelect = '';
    this.selectedCIicon = false;
    for (const droppedFile of files) {
      this.disableBrowse = false;
      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          const fileSize = file.size;
          const maxAllowedSize = 2000000; // bytes for 2MB

          if (fileSize > maxAllowedSize) {
            this.errorFileSize = true;
            this.isValid = 1;
          } else {
            this.errorFileSize = false;
            this.selectedImg = file;
          }

          const extension = (file.name).split('.').pop();

          if (extension == 'jpeg' || extension == 'jpg' || extension == 'JPG' || extension == 'JPEG' || extension == 'png' || extension == 'PNG') {
            this.errorFileType = false;
            this.selectedImg = file;
            this.isValid = 0;
          } else {
            this.errorFileType = true;
            this.isValid = 2;
          }

          // Preview Import Image
          const reader = new FileReader();
          fileEntry.file(file => {
            reader.readAsDataURL(file);
            reader.onload = () => {
              this.iconImport = reader.result;
              if (this.iconImport) {
                this.selectedCIicon = true;
              }
            };
          });

        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
      }
    }
  }
 
  div1Function(check): void {
    this.div1 = true;
    this.div2 = false;
    if (check === 1) {
      this.tab = 'tab1';
    } else {
      this.tab = 'tab2';
    }

  }

  div2Function(check): void {
    this.div2 = true;
    this.div1 = false;
    if (check === 2) {
      this.tab = 'tab2';
    } else {
      this.tab = 'tab1';
    }

  }

  div3Function(check): void {
    this.div3 = true;
    this.div4 = false;
    if (check === 1) {
      this.tab = 'tab1';
    } else {
      this.tab = 'tab2';
    }

  }

  div4Function(check): void {
    this.div4 = true;
    this.div3 = false;
    if (check === 2) {
      this.tab = 'tab2';
    } else {
      this.tab = 'tab1';
    }

  }


  showModalDialog(): void {
    this.displayModal = true;
  }

  /*Cancel button handling*/
  cancelImagemodal(): void {
    if (this.getCIid == '') {
      this.seletedIcon = '';
      this.disableBrowse = true;
    }
    this.displayModal = false;
    this.errorFileSize = false;
    this.errorFileType = false;
    this.file = [];
    this.div3 = true;
    this.div4 = false;
    this.tab = 'tab1';
    this.disableBrowse = true;
  }

  cancelCIDetails(): void {
    // this.ciTypeDetailForm.reset();
    // this.seletedIcon = '';
    // this.imgDiv = false;
    // this.noIcon = true;
    // this.yesIcon = false;
    this.router.navigate(['configuration/citypelist']);
  }
   /* Enable tab*/
  openNext() {
    this.tabIndex = (this.tabIndex === 2) ? 0 : this.tabIndex + 1;
    this.getTabIndexValue.emit(this.tabIndex);
  }
  openPrev() {
    this.tabIndex = (this.tabIndex === 0) ? 2 : this.tabIndex - 1;
  }

  /* Edit CI Type*/
  editCI(ciId): void {
    this.isCiDetailEdit = true;
    this.commonSevice.updateLoaderStatus(true);
    this.citypeService.getCIData(ciId).subscribe(data => {
      const sourceCIval = {
        ciTypeName: data.ciTypeName,
        ciId: data.id,
        parentCITypeId: data.parentCITypeId,
        parentCIName: data.parentName,
        hierarchy: data.hierarchy
      };
      this.commonSevice.updateCiDetailInfo(sourceCIval);
      this.ciTypeDetailForm.patchValue({
        id: data.id,
        ciTypeName: data.ciTypeName,
        parentCIType: { code: data.parentCITypeId, name: data.parentName },
        description: data.description
      });
      this.currentCiName = data.ciTypeName;

      if (data.imageLocation) {
        if (data.icon == 'import') {
          this.viewIcon = '';
          this.seletedImport = this.pathUrl + '' + data.imageLocation;
          this.iconSelected = 'import';
          
        } else {
          this.seletedImport = '';
          this.viewIcon = data.imageLocation;
          this.iconSelected = 'icon';
          // this.imageUpload = false;
        }
        if (this.viewIcon || this.seletedImport) {
          this.hideBrowse = false;
        }
        this.ciTypeDetailForm.get('selectedIconImage').setValue(this.viewIcon);
        this.imgDiv = true;
        this.noIcon = false;
        this.yesIcon = true;
      }
      this.commonSevice.updateLoaderStatus(false);
      this.ciError = false;
    }, () => {
      this.commonSevice.updateLoaderStatus(false);
    });

  }

 /* Get Parent CI Name */
 getParentData(): void {
  this.commonSevice.CiHeaderData.subscribe(data => {
    if (data) {
      this.ciTypeDetailForm.patchValue({
        parentCIType: { code: data.parentCITypeId, name: data.parentCIName },
        
      });
      // this.disableParent = true;
      if(data.parentCITypeId !== ""){
        this.disableParent = true;
      }
      else{
        this.disableParent = false;
      }
      
    }

  });
 }
}
